.offers{
    width: 65%;
    height: 100vh;
    display: flex;
    margin: auto;
    margin-top: 100px;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg,#fde1ff 0%, #e1ffea22 60%);
}


.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* background-color: red; */
    margin-right: 90px;
}

.offers-left h1{
    color: #171717;
    font-size: 80px;
    font-weight: 600;
}

.offers-left p{
    color: #171717;
    font-size: 22px;
    font-weight: 600;
}

.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background:#ff4141 ;
    border: none;
    color: white;
    margin-top: 20px;
    margin-left: 72px;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;

}

.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: 50px;
    
    padding-top: 140px;
    /* background-color: #ff4141; */
    margin-top: 60px;
    margin-left: 75px;

}

